@use "~@immunespace/theme/dist/immunespace";
@use "~@immunespace/theme/dist/immunespace.responsive" as responsive;

body {
  color: immunespace.$blue;
  font-family: immunespace.$nunito-sans;
  font-weight: 400;
  margin: 0;
  padding: 0;
  background: immunespace.$white;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
p {
  font-family: immunespace.$nunito-sans;
}

.container {
  padding: 0 1.5rem;
  max-width: 960px;
  margin: 0 auto;
  width: 100%;

  @include responsive.media-query(tab) {
    // padding: 0 .5rem;
  }

  @include responsive.media-query(desk) {
    padding: 0;
  }
}

.h-o-d {
  @include responsive.media-query(desk) {
    display: none;
  }
}

.h-o-m {
  @include responsive.media-query(mobile) {
    display: none;
  }
}

.is-disabled {
  display: none;
}

.is-hidden {
  display: none !important;
}

/** --- Bobber --------- */
@keyframes bobberLtr {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translate(0.375rem, 0);
  }
}
